<template>
  <div class="menu-wrapper">
    <h1>БАРНАЯ&nbsp;КАРТА</h1>
    <p class="note">Нажми на карточку коктейля, чтобы посмотреть его рецепт</p>
    <div v-for="item in drinks" :key="item.id" class="drinks">
      <p class="drinkType">{{ item.drinkType }}</p>
      <div v-for="cocktail in item.cocktails" :key="cocktail.id" class="cocktail">
        <div @click="openPopup(cocktail)">
          <div class="cocktail__title">
            <p>{{ cocktail.title.toLocaleUpperCase() }}</p>
          </div>
          <div class="cocktail__ingredients">
            <p>{{ cocktail.ingredients.map(ingredient => ingredient.name).join(', ') }}</p>
          </div>
        </div>
      </div>
    </div>

    <CocktailPopup
      v-if="selectedCocktail"
      :cocktail="selectedCocktail"
      :isVisible="isPopupVisible"
      @close="closePopup"
    />
  </div>
</template>

<script>
import drinks from '../assets/resources/drinks';
import CocktailPopup from './CocktailPopup.vue'; // Импортируйте ваш попап
import qr from '../assets/img/qr-code.svg';

export default {
  name: 'MenuComponent',
  components: {
    CocktailPopup,
  },
  data() {
    return {
      drinks,
      qr,
      selectedCocktail: null,
      isPopupVisible: false,
    };
  },
  methods: {
    openPopup(cocktail) {
      this.selectedCocktail = cocktail;
      this.isPopupVisible = true;
      document.body.classList.add('no-scroll'); // Блокируем скролл заднего фона
    },
    closePopup() {
      this.selectedCocktail = null;
      this.isPopupVisible = false;
      document.body.classList.remove('no-scroll'); // Восстанавливаем скролл заднего фона
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.no-scroll {
  overflow: hidden;
}
h1 {
  font-family: "Menege Light", sans-serif;
  font-size: 28px;
  letter-spacing: 3px;
  color: #efefef;
  text-align: center;
  padding-bottom: 8px;
  margin: 0;
}
.note {
  color: #FFFFFF;
  font-family: 'Rothek ExtraLight';
  text-align: center;
  font-size: 12px;
  padding-bottom: 32px;
  opacity: 80%;
}
.menu-wrapper {
  margin: 0px auto;
  padding: 20px;
  position: relative;
  z-index: 10;
}
.drinks {
  margin-bottom:32px;
}
.drinkType {
  font-size: 20px;
  font-weight: 600;
  color: #FFFFFF;
  font-family: 'Rothek ExtraLight';
  text-align: center;
  padding: 6px;
  background-color: red;
  margin-bottom: 16px;
}
.cocktail {
  color: #FFFFFF;
  margin: 16px 0;
  padding: 16px;
  border: 1px solid #ffffff35;
  background-color: #1c1c1c50;
  border-radius: 12px;
  cursor: pointer;
  &:hover{
    background-color: #ff000050;
    border: 1px solid #ffffff65;
  }
  &:active{
    background-color: #ff000050;
    border: 1px solid #ffffff65;
  }
  &:focus{
    background-color: #ff000050;
    border: 1px solid #ffffff65;
  }
  &__title {    
    padding-bottom: 12px;
    p {
      font-family: 'Rothek ExtraLight';
      font-size: 16px;
      font-weight: 600;
      color: #FFFFFF;
    }
  }
  &__qr {
    width: 100px;
  }
  &__recipe {
    font-family: 'Rothek ExtraLight';
    display: none;
    font-size: 16px;
    background: #ffffff;
    position: absolute;
    width: 100%;
    height: 100%;
    ol {
      list-style-position: inside;
    }
    li {
      padding: 5px 0;
    }
    &.active {
      display: block;
    }
  }
  &__ingredients {
    font-family: 'Rothek ExtraLight';
    font-size: 16px;
    line-height: 20px;
    color: #dcdcdc;
    p {
      display: inline-block;
    }
  }
}
</style>
